n\n\n\n\n\n\n\n\n\n\n\n\n.title {\n  // 设置文字不折行\n  word-wrap: none;\n  white-space: nowrap;\n  font-size: 1.15rem;\n  font-weight: 700;\n  color: $black-87;\n}\n\n@mixin select {\n  display: flex;\n  align-items: center;\n  padding: 0.2rem 1.5rem;\n  font-size: 1rem;\n  border-radius: 10rem;\n  margin: 0.6rem;\n  border: none;\n  outline: none;\n  cursor: pointer;\n  background-color: $gray-04;\n  transition: 0.5s;\n}\n.select {\n  @include select;\n}\n\n.select:hover {\n  transform: translate(0, -0.2rem);\n  box-shadow: 0px 10px 20px $black-08;\n  transition: transform 0.2s;\n}\n\n.select:active {\n  background-color: #fff;\n  transform: translate(0, -0.1rem);\n  transition: transform 0.2s;\n}\n\n// 选定状态下的样式\n.selected {\n  @include select;\n  background-color: bisque;\n  p {\n    color: #fff;\n  }\n}\n\n.selected:hover {\n  transform: translate(0, -0.2rem);\n  box-shadow: 0px 10px 20px $black-08;\n  transition: transform 0.2s;\n}\n\n.selected:active {\n  background-color: #fff;\n  transform: translate(0, -0.1rem);\n  transition: transform 0.2s;\n}\n\n.logo {\n  width: 2rem;\n  height: 2rem;\n  object-fit: contain;\n  margin-right: 0.5rem;\n}\n"]}]);
